// base.scss
.b-entry{
    .b-tit{
        padding: 0 0 22px;
    }
    h1,
    h2,
    h3,
    h4,
    h5,
    h6{
        padding: 10px 0;
    }
    h1{
        font-size: 24px;
        line-height: 26px;
    }
    h2{
        font-size: 22px;
        line-height: 24px;
    }
}
// main.scss
.b-cart-open{
	width: 300px;
	padding-bottom: 200px;
	left: calc(100% + 300px);
	&__tit{
		margin-bottom: 5px;
	}
	&__foot{
		&-total{
			padding: 0;
		}
		&-col{
			width: auto;
			padding: 20px 0 0;
			float: none;
			&:first-child{
				float: none;
			}
		}
	}
	&.is-open{
		left: calc(100% - 300px);
	}
}
.b-cart-post{
	&__img{
		display: none;
	}
	&__txt{
		width: calc(100% - 21px);
		padding: 0;
	}
	&__btn{
		padding-top: 3px;
	}
}
// HEADER
.b-header{
    &__search{
        display: none;
    }
}
// MAIN
// home page
.b-main-screen{
	&__txt{
		width: 100%;
		&-tit{
			font-size: 22px;
			line-height: 26px;
		}
	}
	&__img{
		display: none;
	}
}
.b-our-plus{
	padding: 70px 0 0;
	.b-head{
		padding: 0 0 10px;
	}
	&__row{
		margin: 0;
	}
	&__col{
		width: auto;
		padding-left: 0;
		padding-right: 0;
		float: none;
	}
}
.b-action{
	padding: 70px 0 0;
}
.b-popular{
	padding: 70px 0;
	&__col{
		width: auto;
		margin: 0;
		display: block;
	}
}
.b-prev-prod{
	padding-left: 20px;
	padding-right: 20px;
	&:hover{
		padding-left: 20px;
		padding-right: 20px;
	}
}
.b-news{
	padding: 70px 0 0;
}
.b-about{
	padding: 70px 0 0;
	.b-head{
		padding: 0 0 40px;
	}
}
// sub page
.b-suppage-body{
	&__img{
		margin: 0 0 32px;
	}
}
// catalog
.b-catlog-page{
	&__left{
		width: auto;
		padding: 0 0 0 0;
		float: none;
	}
	&__right{
		width: auto;
		float: none;
	}
}
.b-catlog-filter{
	&__col{
		&--price{
			justify-content: space-between;
		}
	}
}
.b-catlog-sort{
	margin: 0 0 30px;
}
.b-catlog-grid{
	margin: 0;
	padding: 0;
	&__col{
		width: auto;
		padding-left: 0;
		padding-right: 0;
		float: none;
	}
}
// product
.b-prod-entry{
	padding: 0 0 40px;
	&__foot{
		text-align: center;
		&-info,
		&-price,
		&-numb,
		&-btn,
		&-input{
			width: 50%;
		}
		&-info{
			&--label{
				width: 100%;
			}
		}
	}
}
.b-vol-list{
	&__item{
		width: 33.33%;
		a{
			width: 100%;
		}
	}
}
.b-prod-info{
	padding: 0 0 70px;
}
.b-prod-slider{
	margin: 0 -20px;
	padding: 0 0 60px;
	&__item{
		padding: 20px;
	}
	.slick-arrow{
		margin-top: 0;
		top: auto;
		bottom: 0;
		right: calc(50% - 60px);
		&.slick-prev{
			right: 50%;
		}
	}
}
// delivery
.b-deliv-page{
	padding-bottom: 40px;
	&__row{
		&-img{
			padding: 0 60px 30px 0;
		}
	}
	&__img{
		height: 220px;
		&-txt{
			width: 220px;
			height: 30px;
			margin-top: -15px;
			font-size: 37px;
			line-height: 30px;
			letter-spacing: 1px;
			left: -98px;
		}
		img{
			margin-right: -70px;
		}
	}
	&--bot{
		padding-bottom: 0;
		.b-deliv-page__row{
			&-img{
				padding-left: 60px;
				padding-right: 0;
			}
		}
		.b-deliv-page__img{
			&-txt{
				left: auto;
				right: -96px;
			}
			img{
				margin-left: -70px;
				margin-right: 0;
			}
		}
	}
}
// contact
.b-contact-page{
	padding-bottom: 70px;
}
.b-photo-grid{
	&__col{
		width: 50%;
	}
}
// news
.b-news-page{
	.b-head{
		padding-bottom: 40px;
	}
}
.b-news-grid {
	margin: 0;
	&__col{
		width: 100%;
		padding: 0;
	}
}