*,
*::before,
*::after {
    box-sizing: border-box;
}
html,
body {
    width: 100%;
    min-height: 100%;
    height: auto !important;
    padding: 0;
    margin: 0;
    background: $color-white;
    font-family: $font-family-mont;
    font-size: 50px;
    font-weight: 300;
    line-height: 1.714em;
    letter-spacing: 0.071em;
    color: $color-black;
    position: relative;
}
div,
ul,
ol,
li,
p,
h1,
h2,
h3,
h4,
h5,
h6,
form,
select,
input,
textarea,
img,
dl,
dt,
dd,
table,
div,
td,
theder,
button,
blockquote,
article,
aside,
audio,
canvas,
command,
datalist,
details,
figcaption,
figure,
footer,
header,
hgroup,
keygen,
mark,
menu,
meter,
main,
nav,
output,
progress,
rp,
rt,
ruby,
section,
source,
summary,
time,
video,
wbr {
    padding: 0;
    margin: 0;
}
article,
aside,
audio,
canvas,
command,
datalist,
details,
figcaption,
figure,
footer,
header,
hgroup,
keygen,
mark,
menu,
main,
meter,
nav,
output,
progress,
rp,
rt,
ruby,
section,
source,
summary,
time,
video,
wbr,
address {
    display: block;
    font-style: normal;
}
ul,
ol,
li {
    list-style: none;
}
*:active,
*:focus,
a,
input,
textarea,
button {
    outline: none !important;
    -moz-appearance: none;
    -webkit-appearance: none;
    -moz-appearance: none;
    -webkit-appearance: none;
    appearance: none;
    box-shadow: none;
}
input,
textarea,
button{
    font-family: $font-family-mont;
    border-radius: 0;
}
textarea{
	resize: none;
}
::-moz-focus-inner{
    border: none;
}
a {
    color: $color-body;
    transition: all 0.3s ease;
    text-decoration: underline;
    &:hover{
        color: $color-green;
        text-decoration: none;
    }
}
strong{
	font-weight: 500;
}
::-webkit-input-placeholder {
    opacity: 1;
    transition: opacity 0.3s ease;
}
::-moz-placeholder {
    opacity: 1;
    transition: opacity 0.3s ease;
}
:-moz-placeholder {
    opacity: 1;
    transition: opacity 0.3s ease;
}
:-ms-input-placeholder {
    opacity: 1;
    transition: opacity 0.3s ease;
}
:focus {
    &::-webkit-input-placeholder {
        opacity: 0;
        -moz-transition: opacity 0.3s ease;
    }
}
:focus {
    &::-moz-placeholder {
        opacity: 0;
        -moz-transition: opacity 0.3s ease;
    }
}
:focus {
    &:-moz-placeholder {
        opacity: 0;
        -moz-transition: opacity 0.3s ease;
    }
}
:focus {
    &:-ms-input-placeholder {
        opacity: 0;
        transition: opacity 0.3s ease;
    }
}
.cf {
    &:before,
    &:after {
        content: '';
        display: block;
        overflow: hidden;
    }
    &:after {
        clear: both;
    }
}
.b-flex {
    display: flex;
    flex-direction: row;
    flex-wrap: nowrap;
    justify-content: flex-start;
    align-content: stretch;
    &--middle {
        align-items: center;
    }
    &--space-between {
        justify-content: space-between;
    }
    &--bottom {
        align-items: flex-end;
    }
    &--end,
    &--end-col {
        align-items: flex-end;
    }
    &--middle-col,
    &--bottom-col,
    &--end-col,
    &--top-col {
        flex-direction: column;
    }
    &--middle-col {
        justify-content: center;
    }
    &--bottom-col {
        justify-content: flex-end;
    }
    &--top-col {
        justify-content: flex-start;
    }
    &--right {
        justify-content: flex-end;
    }
    &--center {
        justify-content: center;
    }
}
.b-center {
	padding: 0 4.643em;
    position: relative;
    z-index: 10;
}
.b-entry{
    .b-tit{
        padding: 0 0 3.000em;
        text-align: center;
    }
    p{
        padding: 0 0 1.429em;
    }
    h1,
    h2,
    h3,
    h4,
    h5,
    h6{
        font-weight: 500;
        text-transform: uppercase;
        color: $color-body;
    }
    h1{
        padding: 0.550em 0;
        font-size: 2.857em;
        line-height: 1.100em;
    }
    h2{
        padding: 0.611em 0;
        font-size: 2.571em;
        line-height: 1.111em;
    }
    h3{
        padding: 0.917em 0;
        font-size: 1.714em;
        line-height: 1.167em;
    }
    h4{
        padding: 1.571em 0;
        font-size: 1em;
        font-weight: 300;
        line-height: 1.286em;
        color: $color-grey;
    }
    h5{
        padding: 1.571em 0;
        font-size: 1em;
        line-height: 1.286em;
        text-transform: none;
    }
    h6{
        padding: 2.200em 0;
        font-size: 0.714em;
        line-height: 1.200em;
    }
}
.b-list{
    li{
        padding: 0 0 0 1.143em;
        position: relative;
        &:before{
            width: 0.286em;
            height: 0.286em;
            display: block;
            content: '';
            background: $color-green;
            position: absolute;
            top: 0.714em;
            left: 0;
            z-index: 1;
        }
    }
}
.b-head{
    text-align: center;
    .b-tit{
        padding: 0 0 0.958em;
    }
}
.b-tit,
h1.b-tit{
    font-size: 1.714em;
    font-weight: 500;
    line-height: 1.083em;
    letter-spacing: 0.083em;
    text-transform: uppercase;
    color: $color-body;
}
.b-sub-tit{
    font-size: 1.286em;
    font-weight: 500;
    line-height: 1.222em;
    letter-spacing: 0.111em;
    color: $color-body;
    &--sm{
        font-size: 1em;
        font-weight: 300;
        line-height: 1.143em;
        color: $color-grey;
    }
}
.b-crumbs{
    font-size: 0.786em;
    font-weight: 500;
    line-height: 1.273em;
    color: $color-body;
    position: relative;
    a{
        margin: 0 4.091em 0 0;
        display: inline-block;
        vertical-align: middle;
        color: $color-grey;
        text-decoration: none;
        position: relative;
        &:before{
            width: 0.455em;
            height: 0.091em;
            display: block;
            content: '';
            background: $color-grey;
            position: absolute;
            top: 50%;
            right: -2.273em;
        }
        &:hover{
            color: $color-body;
            text-decoration: none;
        }
    }
    span{
        display: inline-block;
        vertical-align: middle;
    }
}
.b-tabs-nav{
    padding: 0 0 4.429em;
    line-height: 1em;
    text-align: center;
    ul{
        li{
            padding: 0 0.714em 0.500em;
            display: inline-block;
            a{
                display: block;
                color: $color-grey;
                text-decoration: none;
                position: relative;
                &:before{
                    width: 0;
                    height: 0.143em;
                    display: block;
                    content: '';
                    background: $color-green;
                    transition: all 0.3s ease;
                    position: absolute;
                    bottom: 0.143em;
                    left: 50%;
                    z-index: 1;
                }
                span{
                    display: inline-block;
                    position: relative;
                    z-index: 2;
                }
                &:hover{
                    color: $color-body;
                    &:before{
                        width: 100%;
                        left: 0;
                    }
                }
            }
            &.ui-tabs-active{
                a{
                    color: $color-body;
                    &:before{
                        width: 100%;
                        left: 0;
                    }
                }
            }
        }
    }
    &--left{
        padding: 0 0 3.071em;
        text-align: left;
        ul{
            li{
                padding-left: 1.429em;
                padding-right: 0;
                &:first-child{
                    padding-left: 0;
                }
            }
        }
    }
}
.b-pagenav{
    padding: 3.571em 0 0;
    line-height: 2.857em;
    text-align: center;
    position: relative;
    &__arrow{
        width: 4.286em;
        height: 2.857em;
        display: inline-block;
        vertical-align: middle;
        background: $color-light;
        transition: all 0.3s ease;
        text-indent: -9999em;
        text-decoration: none;
        overflow: hidden;
        cursor: pointer;
        position: relative;
        z-index: 100;
        &:before{
            width: 100%;
            margin: -0.500em 0 0 0;
            display: block;
            content: "\e900";
            font-family: $font-family-icomoon !important;
            font-size: 0.571em;
            line-height: 1em;
            color: $color-body;
            text-align: center;
            speak: none;
            font-style: normal;
            font-weight: normal;
            font-variant: normal;
            text-transform: none;
            line-height: 1;
            -webkit-font-smoothing: antialiased;
            -moz-osx-font-smoothing: grayscale;
            text-indent: 0;
            transition: all 0.3s ease;
            position: absolute;
            top: 50%;
            left: 0;
        }
        &--next{
            &:before{
                transform: rotate(180deg);
            }
        }
        &:hover{
            background: $color-green;
            &:before{
                color: $color-white;
            }
        }
    }
    &__number{
        padding: 0 1.944em;
        display: inline-block;
        vertical-align: middle;
        font-size: 1.286em;
        color: $color-green;
        small{
            font-size: 0.667em;
            color: $color-body;
        }
    }
}