@import "variables";
@import "functions";
@import "mixins";
@import "sprites";
@import "fonts";
@import "vendor";
@import "base";
.b-page{
	position: relative;
	overflow-x: hidden;
	z-index: 10;
	&__header{
		width: 100%;
		position: fixed;
		top: 0;
		left: 0;
		z-index: 10;
	}
	&__main{
		min-height: calc(100vh - 22.857em);
		padding-top: 12.143em;
		position: relative;
		z-index: 5;
	}
	&__footer{
		position: relative;
		z-index: 2;
	}
}
.b-cart-open{
	width: 33.071em;
	min-height: 100vh;
	padding: 3.214em 4.786em 13.500em 2.143em;
	background: $color-white;
	box-shadow: 0 0 1.643em rgba(0, 0, 0, .1);
	transition: left 0.3s ease;
	position: absolute;
	top: 0;
	left: calc(100% + 33.071em);
	z-index: 20;
	&__tit{
		margin-bottom: 1.900em;
		padding: 0.100em 0 0 2.500em;
		display: block;
		font-size: 0.714em;
		font-weight: 500;
		line-height: 1.400em;
		text-transform: uppercase;
		color: $color-body;
		position: relative;
		@extend .icon;
	    @extend .icon-cart;
		&:before{
			display: block;
			font-size: 1.200em;
			line-height: 1em;
			transform: translate(0, -50%);
			position: absolute;
			top: 50%;
			left: 0;
		}
	}
	&__foot{
		position: absolute;
		bottom: 4.929em;
		left: 2.143em;
		right: 4.786em;
		&-total{
			padding: 0 0 1.643em;
			text-align: right;
			span{
				padding-left: 0.125em;
				font-size: 1.714em;
				font-weight: 500;
				color: $color-body;
			}
		}
		&-col{
			width: calc(50% - 1.071em);
			float: right;
			&:first-child{
				float: left;
			}
		}
		.b-btn{
			width: 100%;
			min-width: 100%;
		}
	}
	&.is-open{
		left: calc(100% - 33.071em);
	}
}
.b-cart-post{
	padding: 1.357em 0 1.214em;
	border-bottom: 0.071em solid $color-greyL;
	position: relative;
	&__img{
		width: 4.286em;
		height: 4.286em;
		margin-right: 0.714em;
		padding: 0.571em;
		float: left;
		border: 0.071em solid $color-greyL;
		line-height: 3.000em;
		text-align: center;
		position: relative;
		img{
			height: 100%;
			display: inline-block;
			vertical-align: middle;
		}
	}
	&__txt{
		width: calc(100% - 7.167em);
		padding: 0.417em 0.833em 0 0;
		float: left;
		font-size: 0.857em;
		line-height: 1.500em;
		position: relative;
		&-tit{
			padding: 0 0 0.250em;
			font-weight: 500;
			color: $color-body;
		}
		&-sum{
			width: 50%;
			float: left;
		}
		&-price{
			width: 50%;
			float: right;
			color: $color-body;
			text-align: right;
		}
	}
	&__btn{
		width: 1.143em;
		margin-right: -0.143em;
		padding: 0.500em 0 0 0;
		float: right;
		position: relative;
	}
}
// HEADER
.b-header{
	background: $color-white;
	box-shadow: 0 0.071em 0.500em rgba(0, 0, 0, .1);
	transition: all 0.3s ease;
	&__top{
		width: 100%;
		padding: 1.857em 0 2.000em;
		border-bottom: 0.071em solid $color-greyL;
		transition: all 0.3s ease;
		position: relative;
		@extend .b-flex;
        @extend .b-flex--bottom;
        @extend .b-flex--space-between;
        &-logo{
        	position: relative;
        	z-index: 1;
        }
		&-info{
			white-space: nowrap;
			position: relative;
			z-index: 2;
		}
	}
	&__bot{
		width: 100%;
		height: 5.000em;
		transition: all 0.3s ease;
		position: relative;
		@extend .b-flex;
        @extend .b-flex--middle;
        @extend .b-flex--space-between;
	}
	&__search,
	&__cart{
		padding: 0 0 0.143em 2.357em;
		display: inline-block;
		vertical-align: middle;
	}
	&__search{
		width: 7.857em;
	}
	&.fix{
		box-shadow: 0 0.071em 0.500em rgba(0, 0, 0, .1);
		.b-header__top{
			padding: 0.714em 0 0.286em;
			&-logo{
				padding: 0 0 0.500em;
			}
		}
		.b-header__bot{
			height: 3.571em;
		}
		.b-logo{
			width: 4.786em;
			height: 1.929em;
		}
	}
}
.page-home{
	.b-header{
		box-shadow: none;
		&.fix{
			box-shadow: 0 0.071em 0.500em rgba(0, 0, 0, .1);
		}
	}
}
.b-logo{
	width: 7.857em;
	height: 3.286em;
	transition: all 0.3s ease;
	position: relative;
	a{
		width: 100%;
		height: 100%;
		display: block;
		background-image: url(../images/svg/logo.svg);
		background-position: 0 0;
		background-repeat: no-repeat;
		background-size: 100% 100%;
		text-indent: -9999em;
		overflow: hidden;
		position: absolute;
		top: 0;
		left: 0;
	}
	&--grey{
		a{
			background-image: url(../images/svg/logo-grey.svg);
		}
	}
}
.b-search{
	width: 5.143em;
	height: 2.143em;
	padding: 0.714em 0;
	transition: all 0.3s ease;
	position: relative;
	overflow: hidden;
	&__btn{
		padding: 0 0 0 2.273em;
		display: block;
		font-size: 0.786em;
		font-weight: 500;
		line-height: 1.091em;
		text-transform: uppercase;
		color: $color-body;
		transition: all 0.3s ease;
		cursor: pointer;
		position: relative;
		right: 0;
		z-index: 1;
		@extend .icon;
	    @extend .icon-search;
		&:before{
			display: block;
			font-size: 1.091em;
			line-height: 1em;
			transform: translate(0, -50%);
			position: absolute;
			top: 50%;
			left: 0;
		}
	}
	&__form{
		width: 29.786em;
		transition: all 0.3s ease;
		position: absolute;
		top: 0;
		right: -29.786em;
		z-index: 2;
		.b-search-form{
			&__btn{
				&:before{
					opacity: 0;
				}
			}
		}
	}
	&.is-open{
		width: 29.786em;
		margin-left: calc(5.143em - 29.786em);
		.b-search__btn{
			right: -35.909em;
		}
		.b-search__form{
			right: 0;
		}
	}
}
.b-search-form{
	height: 2.143em;
	position: relative;
	&__input{
		width: 100%;
		padding: 0.500em 3.000em 0.500em 1.000em;
		display: block;
		border: none;
		border-top: 0.100em solid transparent;
		border-bottom: 0.100em solid $color-greyL;
		background: none;
		font-size: 0.714em;
		font-weight: 500;
		line-height: 1.800em;
		letter-spacing: 0.100em;
		color: $color-grey;
		position: relative;
		z-index: 1;
	}
	&__btn{
		padding: 0.571em;
		border: none;
		background: none;
		display: block;
		transform: translate(0, -50%);
		cursor: pointer;
		position: absolute;
		top: 50%;
		right: 0;
		z-index: 2;
		@extend .icon;
	    @extend .icon-search;
		&:before{
			display: block;
			font-size: 0.857em;
			line-height: 1em;
			position: relative;
		}
	}
}
.b-cart{
	padding: 0.091em 2.182em 0 2.273em;
	display: inline-block;
	font-size: 0.786em;
	font-weight: 500;
	line-height: 1.273em;
	text-transform: uppercase;
	color: $color-body;
	cursor: pointer;
	position: relative;
	@extend .icon;
    @extend .icon-cart;
	&:before{
		display: block;
		font-size: 1.091em;
		line-height: 1em;
		transform: translate(0, -50%);
		position: absolute;
		top: 50%;
		left: 0;
	}
	span{
		min-width: 1.273em;
		height: 1.273em;
		padding: 0 0.182em 0 0.273em;
		display: block;
		background: $color-green;
		color: $color-white;
		text-align: center;
		transform: translate(0, -50%);
		position: absolute;
		top: 50%;
		right: 0;
	}
}
.b-menu{
	font-size: 0.786em;
	font-weight: 500;
	line-height: 0.909em;
	text-transform: uppercase;
	position: relative;
	.b-search-form{
		max-width: 27.273em;
		margin: 0 auto;
		display: none;
	}
	&__btn{
		width: 2.364em;
		height: 2.364em;
		display: none;
		cursor: pointer;
		position: relative;
		z-index: 6;
		span{
			width: calc(100% - 0.455em);
			height: 0.091em;
			display: block;
			background: $color-body;
			transform: translate(0, -50%);
			position: absolute;
			top: 50%;
			left: 0;
			&:before,
			&:after{
				height: 0.091em;
				display: block;
				content: '';
				background: $color-body;
				transition: all 0.3s ease;
				position: absolute;
				left: 0;
			}
			&:before{
				top: -0.545em;
				right: 0.455em;
			}
			&:after{
				bottom: -0.545em;
				right: -0.455em;
			}
		}
		&:hover{
			span{
				background: $color-green;
				&:before,
				&:after{
					background: $color-green;
					right: 0;
				}
			}
		}
		&.is-open{
			span{
				width: 100%;
				background-color: transparent;
				&:after,
				&:before{
					top: 0;
					right: 0;
				}
				&:before{
					transform: rotate(-45deg);
				}
				&:after{
					transform: rotate(45deg);
				}
			}
		}
	}
	&__list{
		white-space: nowrap;
		position: relative;
	}
	&__item{
		padding: 0 0 0 3.091em;
		display: inline-block;
		vertical-align: middle;
		position: relative;
		a{
			display: inline-block;
			color: $color-body;
			text-decoration: none;
			position: relative;
			z-index: 2;
			&:before{
				width: 0;
				height: 0.182em;
				display: block;
				content: '';
				background: $color-green;
				transition: all 0.3s ease;
				position: absolute;
				bottom: 0.091em;
				left: 50%;
				z-index: 1;
			}
			span{
				display: inline-block;
				position: relative;
				z-index: 2;
			}
			&:hover{
				color: $color-body;
				&:before{
					width: 100%;
					left: 0;
				}
			}
		}
		&:first-child{
			padding-left: 0;
		}
		&:hover,
		&.is-active{
			a{
				&:before{
					width: 100%;
					left: 0;
				}
			}
		}
	}
}
.b-phone{
	font-size: 0.786em;
	font-weight: 500;
	line-height: 0.909em;
	text-transform: uppercase;
	color: $color-grey;
	white-space: nowrap;
	position: relative;
	span{
		padding-left: 1.818em;
		display: inline-block;
		vertical-align: top;
		white-space: nowrap;
	}
	a{
		color: $color-grey;
		text-decoration: none;
	}
}
// FOOTER
.b-footer{
	&__top{
		padding: 1.429em 0;
		font-weight: 500;
		color: $color-body;
		text-transform: uppercase;
		position: relative;
		@extend .b-flex;
        @extend .b-flex--bottom;
        @extend .b-flex--space-between;
		&-link{
			width: calc(100% - 5.000em);
			padding: 0 0 0.500em;
			font-size: 0.714em;
			line-height: 1.200em;
			i{
				padding: 0 0.417em;
				display: inline-block;
				vertical-align: middle;
				font-size: 1.200em;
				color: $color-grey;
				&.icon-visa,
				&.icon-mir{
					font-size: 0.900em;
				}
			}
		}
		&-logo{
			width: 7.143em;
			.b-logo{
				margin: 0 auto;
			}
		}
		&-share{
			width: calc(100% - 3.571em);
			padding: 0 0 0.357em;
			text-align: right;
		}
	}
	&__bot{
		padding: 2.600em 0;
		border-top: 0.100em solid $color-greyL;
		font-size: 0.714em;
		font-weight: 300;
		line-height: 1.200em;
		text-align: center;
		color: $color-grey;
		position: relative;
	}
	a{
		text-decoration: none;
	}
}
.b-share{
	font-size: 0.714em;
	line-height: 1.200em;
	white-space: nowrap;
	position: relative;
	&__tit{
		padding-right: 1.111em;
		display: inline-block;
		vertical-align: middle;
	}
	&__list{
		display: inline-block;
		vertical-align: middle;
	}
	&__item{
		padding: 0 0 0 1.111em;
		display: inline-block;
		vertical-align: middle;
		&:first-child{
			padding-left: 0;
		}
	}
	a{
		font-size: 1.333em;
		color: $color-grey;
		text-decoration: none;
		&:hover{
			color: $color-green;
		}
	}
	&--center{
		text-align: center;
	}
}
// MAIN
.b-main{
	padding-bottom: 7.143em;
}
// home page
.b-main-screen{
	padding: 7.857em 7.857em 2.143em;
	background: $color-light;
	position: relative;
	overflow: hidden;
	@extend .b-flex;
	&:before{
		width: 100%;
		height: 100%;
		display: block;
		content: '';
		background: right center url(../images/svg/letters-main.svg) no-repeat;
		background-size: auto 100%;
		position: absolute;
		top: 0;
		right: 0;
		z-index: 1;
	}
	&__txt{
		width: calc(100% - 20.500em);
		padding: 1.071em 0 1.071em 0;
		position: relative;
		z-index: 3;
		@extend .b-flex;
		@extend .b-flex--middle-col;
		&-tit{
			padding: 0 0 1.375em;
			font-size: 2.857em;
			font-weight: 500;
			line-height: 1.200em;
			text-transform: uppercase;
			color: $color-body;
		}
		&-entry{
			padding: 0 1.429em 4.643em 0;
			line-height: 2.143em;
		}
	}
	&__img{
		width: 31.786em;
		margin: 0 -3.071em 0 -8.214em;
		padding: 1.071em 0 0;
		position: relative;
		z-index: 2;
		@extend .b-flex;
		@extend .b-flex--bottom-col;
		img{
			width: 100%;
			display: block;
		}
	}
}
.b-our-plus{
	padding: 10.500em 0 0;
	text-align: center;
	position: relative;
	.b-head{
		padding: 0 0 3.643em;
	}
	&__row{
		margin: 0 -1.429em;
		position: relative;
	}
	&__col{
		width: 25%;
		padding: 2.143em 1.429em 0;
		float: left;
		position: relative;
	}
}
.b-plus-post{
	max-width: 14.286em;
	margin: 0 auto;
	text-align: center;
	position: relative;
	&__icon{
		height: 1.667em;
		margin: 0 0 0.410em;
		font-size: 2.786em;
		line-height: 1.667em;
		color: $color-green;
		.icon-plane{
			height: 2.031em;
			font-size: 0.821em;
			line-height: 2.031em;
		}
		.icon-delivery{
			height: 1.444em;
			font-size: 1.128em;
			line-height: 1.444em;
		}
		.icon-office{
			height: 1.857em;
			font-size: 0.897em;
			line-height: 1.857em;
		}
	}
	&__tit{
		padding: 0 0 0.786em;
		font-size: 1em;
		font-weight: 500;
		line-height: 1.286em;
		color: $color-body;
	}
	p{
		font-size: 0.857em;
		line-height: 1.500em;
	}
}
.b-action{
	padding: 10.000em 0 0;
	position: relative;
	.b-head{
		padding: 0 0 3.286em;
	}
	&__txt{
		width: calc(100% - 28.429em);
		padding: 3.071em 6.071em 0 0;
		float: left;
		position: relative;
		&-entry{
			height: 10.143em;
			margin-bottom: 2.857em;
			position: relative;
			overflow: hidden;
		}
	}
	&__img{
		width: 28.429em;
		padding: 0 0 1.286em;
		float: right;
		position: relative;
		&:before{
			width: 100%;
			display: block;
			content: '';
			background: $color-light;
			position: absolute;
			top: 3.571em;
			bottom: 0;
			left: 0;
			z-index: 1;
		}
		img{
			width: 100%;
			max-width: 16.571em;
			margin: 0 auto;
			display: block;
			position: relative;
			z-index: 2;
		}
	}
}
.b-popular{
	padding: 10.357em 0 0;
	position: relative;
	.b-head{
		padding: 0 0 0.214em;
	}
	&__row{
		text-align: center;
		position: relative;
	}
	&__col{
		width: 20%;
		margin: 0 -0.143em;
		display: inline-block;
		vertical-align: top;
	}
}
.b-prev-prod{
	padding: 2.214em 2.714em 1.929em;
	background: $color-white;
	transition: all 0.3s ease;
	text-align: center;
	position: relative;
	z-index: 1;
	&__img{
		height: 13.571em;
		margin-bottom: 3.714em;
		position: relative;
		img{
			// height: 100%;
			margin: 0 auto;
			display: block;
		}
	}
	&__tit{
		height: 3.571em;
		margin-bottom: 1.714em;
		font-size: 1em;
		font-weight: 500;
		line-height: 1.714em;
		color: $color-body;
		position: relative;
		overflow: hidden;
		a{
			display: block;
			color: $color-body;
			text-decoration: none;
		}
		span{
			display: block;
		}
	}
	&__foot{
		width: 100%;
		height: 1.143em;
		line-height: 1.143em;
		position: relative;
		&-price{
			transition: all 0.3s ease;
			transform: translate(-50%, 0);
			position: absolute;
			top: 0;
			left: 50%;
			&-old{
				opacity: 0;
				transition: all 0.3s ease;
				transform: translate(calc((50% - 100%) + 1.071em), 0);
				text-decoration: line-through;
				position: absolute;
				top: 0;
				left: 50%;
			}
		}
		&-btn{
			transition: all 0.3s ease;
			opacity: 0;
			position: absolute;
			top: 0;
			right: 50%;
		}
		&:hover{
			.b-prev-prod__foot-price{
				transform: translate(0, 0);
				left: 0;
				&-old{
					opacity: 1;
				}
			}
			.b-prev-prod__foot-btn{
				opacity: 1;
				right: 0;
			}
		}
	}
	&:hover{
		margin-left: -0.714em;
		margin-right: -0.714em;
		padding-left: 3.429em;
		padding-right: 3.429em;
		box-shadow: 0 0 1.643em rgba(0, 0, 0, .1);
		z-index: 2;
		.b-prev-prod__foot{
			&-price{
				transform: translate(0, 0);
				left: 0;
				&-old{
					opacity: 1;
				}
			}
			&-btn{
				opacity: 1;
				right: 0;
			}
		}
	}
}
.b-news{
	padding: 8.929em 0 0;
	position: relative;
	.slick-arrow{
		position: absolute;
		bottom: 0;
		right: 19.643em;
		&.slick-prev{
			right: 23.929em;
		}
	}
}
.b-news-slid{
	position: relative;
	&__img{
		height: 35.714em;
		margin-right: 21.429em;
		background: $color-light;
		position: relative;
		overflow: hidden;
		z-index: 1;
		img{
			height: 100%;
			display: block;
		}
	}
	&__txt{
		width: 35.000em;
		padding: 4.643em 0 6.143em 6.786em;
