.b-link{
    padding: 0 0 0 1.700em;
    display: inline-block;
    vertical-align: middle;
    border: none;
    border-top: 0.100em solid transparent;
    border-bottom: 0.100em solid $color-green;
    background: none;
    font-size: 0.714em;
    font-weight: 500;
    line-height: 2.000em;
    letter-spacing: 0.200em;
    text-transform: uppercase;
    color: $color-body;
    text-decoration: none;
    cursor: pointer;
    position: relative;
    &:before{
        width: 0;
        height: 0;
        display: block;
        content: '';
        border-top: 0.500em solid transparent;
        border-bottom: 0.500em solid transparent;
        border-left: 0.600em solid $color-body;
        transition: all 0.3s ease;
        transform: translate(0, -50%);
        position: absolute;
        top: 50%;
        left: 0;
    }
    &:hover{
        color: $color-green;
        &:before{
            border-left-color: $color-green;
            animation: arrow-btn 1s infinite;
        }
    }
}
@keyframes arrow-btn {
    0%{
        left: 0;
    }
    50%{
        left: 0.700em;
    }
    100%{
        left: 0;
    }
}
.b-btn-cart{
    font-size: 1em;
    i{
        font-size: 1.143em;
        line-height: 1em;
    }
}
.b-btn{
    min-width: 14.000em;
    height: 4.000em;
    padding: 1.000em;
    display: inline-block;
    vertical-align: top;
    border: 0.100em solid $color-greyL;
    background: none;
    font-size: 0.714em;
    font-weight: 500;
    line-height: 1.800em;
    letter-spacing: 0.100em;
    text-align: center;
    color: $color-body;
    text-decoration: none;
    text-transform: uppercase;
    transition: all 0.3s ease;
    position: relative;
    cursor: pointer;
    &:hover{
        border-color: $color-green;
        background: $color-green;
        color: $color-white;
    }
    &--bg{
        border-color: $color-body;
        background: $color-body;
        color: $color-white;
    }
    &--lg{
        height: 5.500em;
        line-height: 3.300em;
    }
}
.b-btn-close{
    width: 1.2em;
    height: 1.2em;
    display: block;
    font-size: 1em;
    text-indent: -9999em;
    cursor: pointer;
    overflow: hidden;
    position: relative;
    z-index: 6;
    &:before,
    &:after{
        width: 100%;
        height: 0.143em;
        margin-top: -0.071em;
        display: block;
        content: '';
        background: $color-grey;
        transition: all 0.3s ease;
        position: absolute;
        left: 0;
        top: 50%;
    }
    &:before{
        transform: rotate(-45deg);
    }
    &:after{
        transform: rotate(45deg);
    }
    &:hover{
        &:before,
        &:after{
            background: $color-green;
        }
    }
}

// jq-number
.jq-number {
    max-width: 9.083em;
    display: inline-block;
    vertical-align: middle;
    font-size: 0.857em;
    font-weight: 500;
    line-height: 3.167em;
    position: relative;
    &__field {
        width: 100%;
    }
    &__field {
        input {
            width: 100%;
            height: 3.333em;
            padding: 0 3.333em 0 0.833em;
            border: 0.083em solid $color-greyL;
            background: $color-white;
            font-family: $font-family-mont;
            font-size: 1em;
            font-weight: 500;
            line-height: 3.333em;
            text-align: center;
            color: $color-body;
            -moz-appearance: textfield;
            &::-webkit-inner-spin-button,
            &::-webkit-outer-spin-button {
                margin: 0;
                -webkit-appearance: none;
            }
        }
    }
    &__spin {
        width: 2.500em;
        height: calc(50% + 0.083em);
        display: block;
        border: 0.083em solid $color-greyL;
        background: $color-white;
        transition: all 0.3s ease;
        position: absolute;
        right: 0;
        cursor: pointer;
        z-index: 99;
        &:before {
            width: 0.333em;
            height: 0.333em;
            margin: -0.250em 0 0 -0.167em;
            display: block;
            content: '';
            border-top: 0.083em solid $color-grey;
            border-right: 0.083em solid $color-grey;
            transition: all 0.3s ease;
            position: absolute;
            top: 50%;
            left: 50%;
        }
        &.plus {
            top: 0;
            &:before {
                margin-top: -0.167em;
                transform: rotate(-45deg);
            }
        }
        &.minus {
            bottom: 0;
            &:before {
                transform: rotate(135deg);
            }
        }
        &:hover {
            background: $color-green;
            &:before {
                border-color: $color-white;
            }
        }
    }
}

// select
.jq-selectbox.b-select {
    font-size: 0.714em;
    font-weight: 500;
    line-height: 1.400em;
    text-transform: uppercase;
    text-align: left;
    display: inline-block;
    vertical-align: middle;
    cursor: pointer;
    position: relative;
    z-index: 99;
    select{
        position: absolute;
        top: 0;
        left: 0;
        width: 100%;
        height: 100%;
        margin: 0;
        padding: 0;
        opacity: 0;
    }
    .jq-selectbox__select {
        height: 4.000em;
        padding: 1.000em 4.000em 1.000em 2.000em;
        border: 0.100em solid $color-greyL;
        line-height: 1.800em;
        position: relative;
        z-index: 11;
    }
    .jq-selectbox__select-text {
        width: 100% !important;
        height: 100%;
        position: relative;
        overflow: hidden;
    }
    .jq-selectbox__trigger {
        width: 4.000em;
        height: 100%;
        display: block;
        position: absolute;
        top: 0;
        right: 0;
        &-arrow {
            width: 0.400em;
            height: 0.400em;
            margin: -0.300em 0 0 -0.200em;
            border-top: 0.100em solid $color-grey;
            border-right: 0.100em solid $color-grey;
            transform: rotate(135deg);
            position: absolute;
            top: 50%;
            left: 50%;
        }
    }
    .jq-selectbox__dropdown {
        width: 100%;
        padding-top: 4.000em;
        border: 0.100em solid $color-greyL;
        background: $color-white;
        white-space: normal;
        box-shadow: 0 0 1.500em rgba(64, 72, 90, .1);
        position: absolute;
        top: 0;
        left: 0;
        z-index: 10;
        ul {
            li {
                padding: 0 2.000em 1.600em;
                &.selected,
                &:hover {
                    color: $color-green;
                }
                &.sel{
                    display: none;
                }
                &.disabled{
                    opacity: 0.5;
                    cursor: default;
                }
            }
        }
    }
    &.dropup {
        .jq-selectbox__select{
            border-color: transparent;
        }
        .jq-selectbox__dropdown {
            padding-top: 1.600em;
            padding-bottom: 2.400em;
            top: auto;
            bottom: 0px;
        }
    }
    &.opened {
        .jq-selectbox__select{
            border-color: transparent;
        }
        .jq-selectbox__trigger-arrow {
            margin-top: -0.200em;
            transform: rotate(-45deg);
        }
    }
    &--nobd{
        .jq-selectbox__select{
            border: none;
        }
        .jq-selectbox__dropdown{
            border: none;
        }
    }
    &--lg{
        .jq-selectbox__select{
            height: 5.000em;
            padding-top: 1.500em;
            padding-bottom: 1.500em;
        }
        .jq-selectbox__dropdown{
            padding-top: 4.700em;
        }
        &.dropup{
            .jq-selectbox__dropdown {
                padding-top: 1.600em;
                padding-bottom: 3.100em;
            }
        }
    }
}

.b-input,
.b-textarea{
    height: 3.333em;
    padding: 0.833em 1.667em;
    display: inline-block;
    border: 0.083em solid $color-greyL;
    font-size: 0.857em;
    font-weight: 500;
    line-height: 1.500em;
    text-transform: none;
    color: $color-grey;
    position: relative;
    z-index: 11;
}
.b-textarea{
    height: 8.333em;
    line-height: 1em;
}
// radio 
.jq-radio {
    width: 0.714em;
    height: 0.714em;
    margin: 0.071em 1.071em 0px 0px;
    display: inline-block;
    border: 0.071em solid $color-grey;
    border-radius: 50%;
    cursor: pointer;
    font-size: 1em;
    line-height: 0.571em;
    position: relative;
    &__div{
        width: 0.286em;
        height: 0.286em;
        margin: -0.143em 0 0 -0.143em;
        display: block;
        background-color: transparent;
        border-radius: 50%;
        transition: all 0.3s ease;
        position: absolute;
        top: 50%;
        left: 50%;
    }
    &.checked {
        .jq-radio__div {
            background: $color-green;
        }
    }
}

// checkbox 
.jq-checkbox {
    width: 0.714em;
    height: 0.714em;
    margin: 0.071em 1.071em 0px 0px;
    display: inline-block;
    border: 0.071em solid $color-grey;
    cursor: pointer;
    font-size: 1em;
    line-height: 0.571em;
    position: relative;
    &__div{
        width: 0.286em;
        height: 0.286em;
        margin: -0.143em 0 0 -0.143em;
        display: block;
        background-color: transparent;
        transition: all 0.3s ease;
        position: absolute;
        top: 50%;
        left: 50%;
    }
    &.checked {
        .jq-checkbox__div {
            background: $color-green;
        }
    }
}

.jq-checkbox,
.jq-radio {
    input{
        position: absolute;
        z-index: -1;
        margin: 0;
        padding: 0;
        opacity: 0;
    }
}
.b-lb-input{
    cursor: pointer;
}