// form.scss
.b-btn{
    padding: 10px;
    font-size: 10px;
    letter-spacing: 0;
    &--lg{
    	height: 45px;
    	line-height: 23px;
    }
}
// base.scss
.b-center {
	padding: 0 15px;
}
.b-entry{
	&__foot{
		display: block;
		&-col{
			padding: 0 0 20px;
		}
	}
    .b-tit{
        padding: 0 0 32px;
    }
    h1,
    h2,
    h3,
    h4,
    h5,
    h6{
        padding: 12px 0 15px;
    }
    h1{
        font-size: 30px;
        line-height: 34px;
    }
    h2{
        font-size: 26px;
        line-height: 30px;
    }
    h3{
        font-size: 20px;
        line-height: 24px;
    }
}
h1.b-tit{
    font-size: 24px;
    line-height: 26px;
}
.b-tabs-nav{
	&--left{
       text-align: center;
    }
}
// main.scss
.b-page{
	&__main{
		min-height: calc(100vh - 300px);
		padding-top: 150px;
	}
}
.b-cart-open{
	width: 403px;
	padding: 20px 20px 140px 20px;
	left: calc(100% + 403px);
	&__tit{
		margin-bottom: 10px;
	}
	&__foot{
		bottom: 20px;
		left: 20px;
		right: 20px;
	}
	&.is-open{
		left: calc(100% - 403px);
	}
}
// HEADER
.b-header{
	&__top{
		padding: 18px 0 21px;
	}
	&__search,
	&__cart{
		padding-left: 15px;
	}
	&__search{
		width: 85px;
	}
}
.b-search{
	&__form{
		width: 217px;
		right: -417px;
	}
	&.is-open{
		width: 217px;
		margin-left: calc(72px - 217px);
		.b-search__btn{
			right: -195px;
		}
		.b-search__form{
			right: 0;
		}
	}
}
.b-menu{
	.b-search-form{
		display: block;
	}
	&__box{
		padding-left: 15px;
		padding-right: 15px;
	}
	&__list{
		padding: 0 0 20px;
	}
}
.b-phone{
	span{
		padding-left: 10px;
	}
}
// FOOTER
.b-footer{
	&__top{
		padding: 10px 0;
		display: block;
		text-align: center;
		&-link{
			width: auto;
			padding: 10px 0;
		}
		&-logo{
			display: none;
		}
		&-share{
			width: auto;
			padding: 10px 0;
			text-align: center;
		}
	}
	&__bot{
		padding: 16px 0;
	}
}
// MAIN
// home page
.b-main{
	padding-bottom: 50px;
}
.b-main-screen{
	padding: 15px 20px;
	&__txt{
		width: calc(100% - 100px);
		padding-right: 0;
		&-tit{
			padding-bottom: 30px;
			font-size: 25px;
			line-height: 33px;
		}
		&-entry{
			padding-bottom: 30px;
			line-height: 22px;
		}
	}
	&__img{
		width: 300px;
		margin: 0 -35px 0 -135px;
	}
}
.b-our-plus{
	padding: 100px 0 0;
	.b-head{
		padding: 0 0 30px;
	}
	&__row{
		margin: 0 -10px;
	}
	&__col{
		width: 50%;
		padding-left: 10px;
		padding-right: 10px;
	}
}
.b-action{
	padding: 100px 0 0;
	.b-head{
		padding: 0 0 30px;
	}
	&__txt{
		width: auto;
		padding-top: 30px;
		padding-right: 0;
		float: none;
		&-entry{
			height: auto;
			margin-bottom: 20px;
		}
	}
	&__img{
		width: auto;
		float: none;
	}
}
.b-popular{
	padding: 100px 0;
	&__nav{
		padding: 0 0 22px;
	}
	&__col{
		width: 50%;
	}
}
.b-news{
	padding: 100px 0 0;
	.slick-arrow{
		right: calc(50% - 60px);
		&.slick-prev{
			right: 50%;
		}
	}
}
.b-news-slid{
	&__img{
		height: auto;
		margin-right: 0;
		img{
			width: 100%;
			height: auto;
		}
	}
	&__txt{
		width: auto;
		padding-top: 40px;
		padding-left: 0;
		background: none;
		position: relative;
		&-tit{
			padding: 0 0 30px;
		}
	}
}
.b-about{
	padding: 100px 0 0;
	.b-head{
		padding: 0 0 60px;
	}
}
// sub page
.b-subpage-head{
	display: none;
}
.b-suppage-body{
	padding-top: 20px;
	&__img{
		height: 250px;
		margin: 0 0 42px;
	}
}
// catalog
.b-catlog-page{
	padding-top: 20px;
	&__left{
		width: auto;
		padding: 0;
		float: none;
	}
	&__right{
		width: auto;
		float: none;
		.b-tit{
			margin-top: 0;
			padding-top: 20px;
			padding-bottom: 22px;
		}
	}
}
.b-catlog-nav{
	margin-bottom: 40px;
	.b-tit{
		padding-bottom: 15px;
	}
}
.b-catlog-filter{
	&__col{
		&--price{
			justify-content: flex-start;
		}
	}
}
.b-catlog-sort{
	margin: 0 0 30px;
}
.b-catlog-grid{
	margin: 0 -10px;
	padding: 0 0 20px;
	&__col{
		width: 50%;
		padding: 0 10px 20px;
		float: left;
	}
}
// product
.b-prod-page{
	padding-top: 20px;
	&__left{
		width: auto;
		padding: 0 60px 50px 0;
		float: none;
	}
	&__right{
		width: auto;
		padding-top: 0;
		float: none;
	}
}
.b-prod-img{
	height: 400px;
	padding: 20px 0;
	position: relative !important;
	top: 0 !important;
	&__txt{
		width: 400px;
		height: 40px;
		margin-top: -20px;
		font-size: 55px;
		line-height: 40px;
		letter-spacing: 5px;
		left: -176px;
	}
	img{
		margin-right: -60px;
	}
}
.b-prod-entry{
	padding: 0 0 70px;
	&__tit{
		font-size: 26px;
		line-height: 26px;
	}
	&__art{
		padding: 0 0 35px;
	}
	&__vol{
		padding: 0 0 40px;
	}
	&__txt{
		padding: 0 0 40px;
	}
	&__foot{
		text-align: center;
		&-info,
		&-price,
		&-numb,
		&-btn,
		&-input{
			padding: 0 10px 30px;
		}
		&-price{
			width: 140px;
		}
		&-btn{
			width: 140px;
			padding-top: 0;
		}
	}
	.b-btn{
		width: 100%;
		min-width: 100%;
	}
}
.b-prod-info{
	padding: 0 0 80px;
	&__row{
		margin-top: 12px;
		display: flex;
	    flex-direction: row;
	    flex-wrap: nowrap;
	    justify-content: flex-start;
	    align-content: stretch;
		&:before,
		&:after{
			display: none;
		}
	}
	&__col{
		max-width: none;
		float: none;
		font-size: 12px;
		line-height: 18px;
		letter-spacing: 0;
		text-align: left;
		span{
			padding-left: 0;
			background: none;
		}
		&:first-child{
			padding-right: 15px;
			font-weight: 500;
			letter-spacing: 1px;
			color: $color-body;
			&:after{
				display: inline-block;
				vertical-align: middle;
				content: ':';
			}
			span{
				padding-right: 0;
			}
		}
	}
}
.b-prod-other{
	.b-head{
		padding: 0 0 20px;
	}
}
// delivery
.b-deliv-page{
	padding-top: 20px;
	padding-bottom: 50px;
	&__row{
		padding: 0 0 40px;
	}
	&__img{
		height: 300px;
		&-txt{
			width: 300px;
			height: 40px;
			margin-top: -20px;
			font-size: 48px;
			line-height: 40px;
			letter-spacing: 2px;
			left: -133px;
		}
	}
	&--bot{
		padding-bottom: 0;
		.b-deliv-page__img{
			&-txt{
				left: auto;
				right: -132px;
			}
		}
	}
}
// post
.b-full-post{
	&__sidebar{
		width: auto;
		padding: 0 0 40px;
		float: none;
		display: flex;
	    flex-direction: row;
	    flex-wrap: nowrap;
	    justify-content: space-between;
	    align-content: stretch;
	    &-col{
	    	padding: 0;
	    }
	}
	&__entry{
		width: auto;
		float: none;
	}
}
// contact
.b-contact-page{
	padding: 20px 0 100px;
	&__map{
		height: 300px;
	}
	&__txt{
		padding: 0 0 40px;
		&-tit{
			padding: 0 0 30px;
			font-size: 24px;
			line-height: 26px;
		}
	}
}
.b-contact-photo{
	.b-photo-grid{
		padding-top: 30px;
	}
}
.b-photo-grid{
	&__col{
		width: 33.33%;
	}
}
// news
.b-news-page{
	padding-top: 20px;
	.b-head{
		padding-bottom: 50px;
	}
}
.b-news-grid {
	margin: 0 -15px;
	&__col{
		width: 50%;
		margin-bottom: 60px;
		padding: 0 15px;
	}
}
// cart
#bx-soa-total-mobile{
	margin-bottom: 30px !important;
}
#bx-soa-order{
	.col-sm-9.bx-soa{
		width: auto;
	}
	.col-sm-3.bx-soa-sidebar{
		width: auto;
	}
}
#bx-soa-delivery{
	.bx-soa-pp-company{
		padding-left: 70px;
		padding-right: 120px;
	}
	.bx-soa-pp-delivery-cost{
		width: 90px;
		right: 20px;
	}
}
#bx-soa-paysystem{
	.bx-soa-pp-company{
		padding-left: 70px;
		padding-right: 30px;
	}
}
.basket-items-list-wrapper{
	.basket-items-list-item-container{
		padding-bottom: 20px;
	}
	.basket-items-list-item-amount{
		padding: 0;
	}
	.basket-item-block-price{
		padding-top: 10px;
	}
}
.basket-checkout-block-total{
	min-width: 10px;
}
.basket-checkout-block{
	display: inline-block;
	min-width: 10px;
	width: auto;
}