// base.scss
.b-center {
	padding: 0 20px;
}
.b-tabs-nav{
	padding: 0 0 42px;
	&--left{
		padding: 0 0 23px;
        ul{
            li{
                padding-left: 15px;
                &:first-child{
                    padding-left: 0;
                }
            }
        }
    }
}
// mains.css
// HEADER
.b-menu{
	&__btn{
		display: block;
	}
	&__box{
		width: 100%;
		padding: 30px 20px;
		display: none;
		white-space: normal;
		background: $color-white;
		position: fixed;
		left: 0;
	}
	&__item{
		padding: 10px 0;
		display: block;
		text-align: center;
	}
}
// MAIN
// home page
.b-main-screen{
	padding-left: 20px;
	padding-right: 30px;
	&:before{
		display: none;
	}
	&__txt{
		padding-right: 0;
		&-tit{
			font-size: 35px;
			line-height: 43px;
		}
	}
}
.b-our-plus{
	&__row{
		margin: 0 -10px;
	}
	&__col{
		padding-left: 10px;
		padding-right: 10px;
	}
}
.b-action{
	&__txt{
		width: calc(100% - 308px);
		padding-right: 37px;
	}
	&__img{
		width: 308px;
	}
}
.b-popular{
	&__row{
		margin: 0 -10px;
	}
	&__col{
		width: 33.33%;
		padding: 20px 10px 0;
	}
}
.b-prev-prod{
	padding-left: 30px;
	padding-right: 30px;
	box-shadow: 0 0 15px rgba(0, 0, 0, .1);
	&__foot{
		&-price{
			transform: translate(0, 0);
			left: 0;
			&-old{
				opacity: 1;
			}
		}
		&-btn{
			opacity: 1;
			right: 0;
		}
	}
	&:hover{
		margin: 0;
		padding-left: 30px;
		padding-right: 30px;
		box-shadow: 0 0 15px rgba(0, 0, 0, .1);
	}
}
.b-news{
	padding: 125px 0 0;
	position: relative;
	.slick-arrow{
		position: absolute;
		bottom: 0;
		right: 265px;
		&.slick-prev{
			right: 325px;
		}
	}
}
.b-news-slid{
	&__img{
		margin-right: 200px;
	}
	&__txt{
		width: 450px;
		padding-left: 65px;
	}
}
// catalog
.b-catlog-page{
	&__left{
		width: 330px;
		padding: 0 30px 0 0;
	}
	&__right{
		width: calc(100% - 330px);
	}
}
.b-catlog-nav{
	padding: 30px 0 20px 0;
	.b-tit{
		padding-left: 30px;
		padding-right: 20px;
	}
	&__item{
		padding-left: 30px;
		padding-right: 20px;
		&-over{
			display: none;
		}
	}
}
.b-catlog-sort{
	margin: 0 0 50px;
}
.b-catlog-grid{
	padding: 0 0 20px;
	&__col{
		width: auto;
		padding: 0 0 30px;
		float: none;
	}
}
// product
.b-prod-page{
	&__left{
		width: 290px;
		padding: 0 80px 0 0;
	}
	&__right{
		width: calc(100% - 290px);
	}
}
.b-prod-entry{
	padding: 0 0 100px;
	&__vol{
		padding: 0 0 50px;
	}
	&__txt{
		padding: 0 0 50px;
	}
	&__foot{
		margin: 0 -10px;
		display: block;
		&-info,
		&-price,
		&-numb,
		&-btn,
		&-input{
			margin: 0 -2px;
			padding: 0 10px;
			display: inline-block;
			vertical-align: middle;
		}
		&-price{
			width: 158px;
		}
		&-input{
			width: calc(100% - 189px);
		}
		&-btn{
			width: 100%;
			padding-top: 30px;
			text-align: center;
		}
	}
	.b-input{
		width: 100%;
		max-width: none;
	}
	.b-btn{
		width: 140px;
		min-width: 140px;
	}
}
.b-prod-slider{
	padding: 0 130px 0 0;
}
// delivery
.b-deliv-page{
	padding-bottom: 70px;
	&__row{
		padding: 0 0 58px;
		display: block;
		&-img{
			width: auto;
			padding: 0 110px 40px 0;
		}
		&-txt{
			width: auto;
		}
	}
	&__img{
		height: 420px;
		&-txt{
			width: 420px;
			letter-spacing: 5px;
			left: -188px;
		}
	}
	&--bot{
		padding-bottom: 0;
		.b-deliv-page__row{
			&-img{
				padding-left: 110px;
				padding-right: 0;
			}
		}
		.b-deliv-page__img{
			&-txt{
				left: auto;
				right: -186px;
			}
		}
	}
}
// post
.b-full-post{
	&__sidebar{
		width: 230px;
		padding-right: 30px;
	}
	&__entry{
		width: calc(100% - 230px);
	}
}
// contact
.b-contact-page{
	display: block;
	&__map{
		width: auto;
	}
	&__txt{
		width: auto;
		padding: 0 0 60px;
	}
}
.b-photo-grid{
	margin: 0 -10px;
	&__col{
		padding: 10px;
	}
}
// news
.b-news-grid {
	margin: 0 -20px;
	&__col{
		padding: 0 20px;
	}
}
.b-news-post{
	&__img{
		&-over{
			padding-left: 30px;
			right: -30px;
			&:before{
				left: 10px;
			}
		}
		img{
			left: -40px;
		}
	}
	&__tit{
		font-size: 12px;
		line-height: 18px;
	}
}
// cart
#bx-soa-order{
	.col-sm-9.bx-soa{
		width: calc(100% - 290px);
	}
	.col-sm-3.bx-soa-sidebar{
		width: 290px;
	}
}
.bx-soa-section,
.bx-soa-section.bx-selected{
	.bx-soa-item-tr{
		border-top: none;
		border-bottom: 1px solid #ededef;
	}
	.bx-soa-item-td{
		border-bottom: none;
	}
}
