// main.scss
// MAIN
// home page
.b-main-screen{
	padding-left: 100px;
	padding-right: 90px;
}
.b-our-plus{
	&__row{
		margin: 0 -15px;
	}
	&__col{
		padding-left: 15px;
		padding-right: 15px;
	}
}
.b-action{
	&__txt{
		width: calc(100% - 348px);
		padding-right: 57px;
	}
	&__img{
		width: 348px;
	}
}
.b-popular{
	&__col{
		width: 25%;
	}
}
.b-suppage-body{
	&__img{
		height: 400px;
		img{
			width: auto;
			height: 100%;
			transform: translate(-50%, 0);
			left: 50%;
		}
	}
}
// catalog
.b-catlog-page{
	&__left{
		width: 350px;
		padding: 0 50px 0 0;
	}
	&__right{
		width: calc(100% - 350px);
	}
}
.b-catlog-nav{
	&__item{
		&-over{
			&-row{
				width: calc(100vw - 77px);
			}
		}
		&:hover{
			.b-catlog-nav__item-over{
				width: calc(100vw - 77px);
			}
		}
	}
}
.b-catlog-grid{
	&__col{
		width: 50%;
	}
}
// product
.b-prod-page{
	&__left{
		width: 350px;
		padding: 0 100px 0 0;
	}
	&__right{
		width: calc(100% - 350px);
	}
}
.b-prod-entry{
	&__foot{
		&-price{
			width: calc(100% - 409px);
			padding-left: 0;
			text-align: left;
		}
		&-numb{
			width: 100px;
			padding-right: 20px;
		}
		&-input{
			padding-right: 20px;
		}
		&-btn{
			width: 120px;
		}
	}
}
.b-prod-slider{
	.slick-list{
		padding: 0;
	}
	&__item{
		padding: 20px 30px;
	}
}
// delivery
.b-deliv-page{
	&__row{
		&-img{
			width: 600px;
			padding: 0 85px 0 0;
		}
		&-txt{
			width: calc(100% - 600px);
		}
	}
	&__img{
		height: 420px;
		&-txt{
			width: 420px;
			letter-spacing: 5px;
			left: -188px;
		}
	}
	&--bot{
		.b-deliv-page__row{
			&-img{
				padding-left: 85px;
				padding-right: 0;
			}
		}
		.b-deliv-page__img{
			&-txt{
				left: auto;
				right: -186px;
			}
		}
	}
}
// contact
.b-contact-page{
	&__txt{
		padding-right: 30px;
	}
}
.b-photo-grid{
	margin: 0 -15px;
	&__col{
		padding: 15px;
	}
}
// news
.b-news-grid {
	margin: 0 -40px;
	&__col{
		padding: 0 40px;
	}
}